html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  margin: 0;
  font: 100 14px 'Roboto';
}

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  font-family: inherit;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  outline: none;
  
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

button:active {
  -webkit-box-shadow: inset 0px 0px 80px 0px rgba(0,0,0,0.25);
          box-shadow: inset 0px 0px 80px 0px rgba(0,0,0,0.25);
}

#wrapper {
  height: 100vh;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

#app {
  width: 320px;
  height: 520px;
  position: relative;
}

.calculator {
  width: 100%;
  height: 100%;
  background: black;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#wrapper .calculator {
  -webkit-box-shadow: 0px 0px 20px 0px #aaa;
          box-shadow: 0px 0px 20px 0px #aaa;
}

.calculator-display {
  color: #00e6ac  ;
  background: #1c191c;
  line-height: 130px;
  font-size: 6em;
  
  -ms-flex: 1 1;
  
      flex: 1 1;
}

.auto-scaling-text {
  display: inline-block;
}

.calculator-display .auto-scaling-text {
  padding: 0 30px;
  position: absolute;
  right: 0;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
}

.calculator-keypad {
  height: 400px;
  
  display: -ms-flexbox;
  
  display: flex;
}

.calculator .input-keys {
  width: 240px;
}

.calculator .function-keys {
  display: -ms-flexbox;
  display: flex;
}

.calculator .digit-keys {
  background: #e0e0e7;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
}

.calculator-key {
  width: 80px;
  height: 80px;
  /*border-top: 1px solid #777;
  border-right: 1px solid #666;*/  
  text-align: center;
  line-height: 80px;
}
.calculator .function-keys .calculator-key {
  font-size: 2em;
}
.calculator .function-keys .key-multiply {
  line-height: 50px;
}
.calculator .digit-keys .calculator-key {
  font-size: 2.25em;
}
.calculator .digit-keys .key-0 {
  width: 160px;
  text-align: left;
  padding-left: 32px;
}
.calculator .digit-keys .key-dot {
  padding-top: 1em;
  font-size: 0.75em;
}
.calculator .operator-keys .calculator-key {
  color: white;
  border-right: 0;
  font-size: 3em;
}

.calculator .function-keys {
  background: #e0e0e7;
  /*background: linear-gradient(to bottom, rgba(202,202,204,1) 0%, rgba(196,194,204,1) 100%);*/
}
.calculator .operator-keys {
  background:  -webkit-gradient(linear, left top, left bottom, from(rgba(33, 150, 243,1)), to(rgba(33, 150, 243,1)));
  background:  -webkit-linear-gradient(top, rgba(33, 150, 243,1) 0%, rgba(33, 150, 243,1) 100%);
  background:  -o-linear-gradient(top, rgba(33, 150, 243,1) 0%, rgba(33, 150, 243,1) 100%);
  background:  linear-gradient(to bottom, rgba(33, 150, 243,1) 0%, rgba(33, 150, 243,1) 100%)
}


